import React, {Component} from 'react';
import {
  Button,
  CardBody,
  CardHeader,
  CardTitle,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Nav,
  NavItem,
  NavLink,
  Row
} from 'reactstrap';
import Table from '../../../components/Table/Table';
import MiniButton from '../../../components/MiniButton';
import ReactLoadingTop from '../../../components/ReactLoadingTop';
import SweetAlertExt from '../../../components/SweetAlertExt';
import validator from 'validator';
import {checkGrantForFetchResponse, checkGrantToDisplay, showAxiosError} from "../../../helper/helper";
import {requestK} from "../../../helper/APIUtils";
import Alert from "react-s-alert";
import {validKepAccount} from "../common/validation/fieldValidation";
//import {KEP_ACCOUNT_CRUD} from "../../../common/authorities/GrantDef";

class KepAccountPage extends Component {

  constructor(props) {
    super(props);
    this.state = {
      alert: null,
      session: null,
      loading: true,
      inputHideAndShow: true,
      inputHideAndShoww: true,
      displayCreateUpdateDialog: false,
      valid: null,
      manual: false,
      content: [],
      total: 0,
      page: 0,
      size: 10,
      searchKey: "",
    };

    this.editValidateDef = {
      account: v => !validator.isEmpty(v),
      tckn: v => v && v.length === 11,
      parola: v => !validator.isEmpty(v),
      sifre: v => !validator.isEmpty(v)
    };

    this.createClicked = this.createClicked.bind(this);
    this.editClicked = this.editClicked.bind(this);
    this.saveClicked = this.saveClicked.bind(this);
    this.deleteClicked = this.deleteClicked.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.changeKepStatus = this.changeKepStatus.bind(this);
    this.editHandleInputChange = this.editHandleInputChange.bind(this);
    this.inputHideAndShow = this.inputHideAndShow.bind(this);
    this.inputHideAndShoww = this.inputHideAndShoww.bind(this);
  }

  fetchData(accessor, direction, page, size) {
    let self = this;
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
    let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
    let params = {
      url: '/kepaccount/readKepAccountAll?page=' + page + '&size=' + size + searchQuery + sortStr,
      method: "get",
    };
    self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
    self.props.reload(true);
    requestK(params)
      .then(function (response) {
        checkGrantForFetchResponse(self, response.data);
        self.setState({
          page: page,
          size: size,
          total: response.data.totalElements,
          list: response.data.content,
          loading: false,
          searching: false
        });
        self.props.reload(false);
      }).catch(function (error) {
      self.setState({loading: false, searching: false});
      self.props.reload(false);
      showAxiosError(error);
    });
  }

  createClicked() {
    this.setState({
      selected: {
        account: "",
        tckn: "",
        parola: "",
        sifre: ""
      },
      valid: {},
      displayCreateUpdateDialog: true
    });
  }

  editClicked(row) {
    let clone = Object.assign({}, row);
    delete clone["_$visited"];
    this.setState({
      selected: clone,
      valid: {},
      displayCreateUpdateDialog: true
    });
  }

  changeKepStatus(selected, status) {
    selected.statusType = status;
    this.setState({selected}, () => {
      this.saveClicked();
    });
  }

  saveClicked() {
    let kepAccount = Object.assign({}, this.state.selected);
    let result = validKepAccount(kepAccount);
    let valid = result.valid;
    if (valid) {
      let self = this;
      let params = {
        url: `/kepaccount/saveKepAccount`,
        method: "post",
        data: kepAccount
      };
      self.setState({loading: true, createUpdateLoading: true, saveDisabledBtn: true});
      requestK(params)
        .then(function (response) {
          let message = response.data;
          self.setState({
            selected: null,
            displayCreateUpdateDialog: false,
            createUpdateLoading: false,
            saveDisabledBtn: false,
            manual: true,
            alert: null
          });
          Alert.success(message, {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 5000
          });
        }).catch(function (error) {
        showAxiosError(error);
        self.setState({loading: false, createUpdateLoading: false, saveDisabledBtn: true});
      });
    } else {
      Alert.error(result.errorMessage, {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 8000
      });
    }
  }

  deleteClicked(row) {
    let self = this;
    let params = {
      url: `/kepaccount/deleteKepAccount`,
      method: "post",
      data: row
    };
    this.setState({deleteLoading: true});
    requestK(params)
      .then(function (response) {
        let message = response.data;
        self.setState({
          selected: null,
          displayCreateUpdateDialog: false,
          manual: true,
          alert: null,
          deleteLoading: false
        });
        Alert.success(message, {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 5000
        });
      }).catch(function (error) {
      showAxiosError(error);
      self.setState({deleteLoading: false, createUpdateLoading: false});
    });
  }

  inputHideAndShow() {

    if (this.state.inputHideAndShow == false) {
      this.setState({inputHideAndShow: true});
    } else {
      this.setState({inputHideAndShow: false});
    }
  }

  inputHideAndShoww() {

    if (this.state.inputHideAndShoww == false) {
      this.setState({inputHideAndShoww: true});
    } else {
      this.setState({inputHideAndShoww: false});
    }
  }


  editHandleInputChange(event) {
    const target = event.target;
    let value = target.type === 'checkbox' ? target.checked : target.value;
    let name = target.name;

    let valid = this.editValidateDef[name] && this.editValidateDef[name](value);
    this.setState((prevState, props) => {
      prevState.selected[name] = value;
      prevState.valid[name] = valid;
      return {
        selected: prevState.selected,
        valid: prevState.valid
      };
    });
  }

  editClassName(name) {
    let valid = this.state.valid[name];
    if (typeof valid !== "boolean")
      return undefined;
    else {
      return {
        valid: valid,
        invalid: !valid
      };
    }
  }

  render() {

    const columns = [
      {
        Header: "Kep Adresi",
        accessor: "account",
        Cell: (item, i) => {
          return item.account;
        },
        width: "25%",
        getTdStyles: item => {
          return {
            textAlign: "left",
            webkitTextFillColor: (item.statusType && item.statusType === 'PASSIVE') ? "darkgray" : null
          };
        }
      },
      {
        Header: "TC Kimlik No",
        accessor: "tckn",
        Cell: (item, i) => {
          return item.tckn;
        },
        width: "15%",
        getTdStyles: item => {
          return {
            webkitTextFillColor: (item.statusType && item.statusType === 'PASSIVE') ? "darkgray" : null
          };
        }
      },
      {
        Header: "Parola",
        accessor: "",
        Cell: (item, i) => {
          return item.parola;
        },
        sortable: false,
        width: "20%",
        getTdStyles: item => {
          return {
            webkitTextSecurity: "disc",
            webkitTextFillColor: (item.statusType && item.statusType === 'PASSIVE') ? "darkgray" : null
          };
        }
      },
      {
        Header: "Şifre",
        accessor: "",
        Cell: (item, i) => {
          return item.sifre;
        },
        sortable: false,
        width: "20%",
        getTdStyles: item => {
          return {
            webkitTextSecurity: "disc",
            webkitTextFillColor: (item.statusType && item.statusType === 'PASSIVE') ? "darkgray" : null
          };
        }
      },
      {
        Header: "İşlemler",
        accessor: "",
        sortable: false,
        width: "10%",
        Cell: row => (
          <div style={{textAlign: 'left'}}>
            {//checkGrantToDisplay(this, KEP_ACCOUNT_CRUD) &&
            <MiniButton title="Düzenle" faIcon="fa-edit" onClick={() => this.editClicked(row)}
                        color="info"/>}
            {//checkGrantToDisplay(this, KEP_ACCOUNT_CRUD) &&
            <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                        onClick={e => {
                          this.setState({
                            alert: (
                              <SweetAlertExt showCancel warning
                                             confirmBtnText="Evet"
                                             cancelBtnText="Hayır"
                                             confirmBtnBsStyle="danger"
                                             cancelBtnBsStyle="default"
                                             title="Emin misiniz?"
                                             onConfirm={() => this.deleteClicked(row)}
                                             onCancel={() => this.setState({alert: null})}
                                             loading={this.state.deleteLoading}
                              >
                                {row.account + " silinecek."}
                              </SweetAlertExt>
                            )
                          });
                        }}/>}
            {(row.statusType === undefined || row.statusType === 'ACTIVE') && //checkGrantToDisplay(this, KEP_ACCOUNT_CRUD) &&
            <button title="Pasife Çek" className="btn-icon btn btn-success btn-sm"
                    style={{width: '50px', fontSize: 'smaller', fontWeight: '600', margin: '0px 3px'}}
                    onClick={() => this.changeKepStatus(row, 'PASSIVE')}>Aktif</button>}
            {(row.statusType && row.statusType === 'PASSIVE') && //checkGrantToDisplay(this, KEP_ACCOUNT_CRUD) &&
            <button title="Active Et" className="btn-icon btn btn-muted btn-sm"
                    style={{width: '50px', fontSize: 'smaller', fontWeight: '600', margin: '0px 3px'}}
                    onClick={() => this.changeKepStatus(row, 'ACTIVE')}>Pasif</button>}
          </div>)

      }
    ];

    return <Row className="baseBackground">
      {this.state.alert}
      {this.renderCreateUpdate()}
      <Col md={12}>
        <Nav className="topbar">
          {//checkGrantToDisplay(this, KEP_ACCOUNT_CRUD) &&
          <NavItem>
            <NavLink href="#" onClick={this.createClicked} style={{color: '#fcfdff', fontSize: '13px'}}>
              <img className="simple-text logo-mini" style={{maxWidth: '28px', marginRight: '6px'}}
                   src={process.env.REACT_APP_CONTEXT_NAME + "/image/plus_sign.png"}/>
              Yeni Oluştur
            </NavLink>
          </NavItem>}
        </Nav>
        <CardHeader style={{marginTop: "56px"}}>
          <CardTitle tag="h4">
            İmza Yetkilisi Kep Hesapları
          </CardTitle>
        </CardHeader>
        <CardBody>
          <Table columns={columns}
                 content={this.state.list}
                 loading={this.state.loading}
                 loadingText="Kayıtlar getiriliyor..."
                 total={this.state.total}
                 page={this.state.page}
                 manual={this.state.manual}
                 size={this.state.size}
                 options={["5", "10", "20", "50", "100"]}
                 fetchData={this.fetchData}
          />
        </CardBody>
      </Col>
    </Row>
  }

  renderCreateUpdate() {
    return <Modal isOpen={this.state.displayCreateUpdateDialog} size="lg"
                  toggle={() => this.setState({displayCreateUpdateDialog: false})}>
      <ModalHeader className="justify-content-center"
                   toggle={() => this.setState({displayCreateUpdateDialog: false})}>
        İmza Yetkilisi Tanımı
      </ModalHeader>
      <ModalBody>
        {this.state.displayCreateUpdateDialog && this.state.selected && <div>
          <Form>
            <FormGroup row>
              <Label sm={4} for="name">Kep Adresi: *</Label>
              <Col sm={8}>
                <Input type="text" name="account" value={this.state.selected.account}
                       onChange={this.editHandleInputChange} {...this.editClassName("account")}/>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={4} for="country">TC Kimlik No:</Label>
              <Col sm={8}>
                <Input type="text" name="tckn" value={this.state.selected.tckn} maxLength={11}
                       onChange={this.editHandleInputChange} {...this.editClassName("tckn")}/>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={4} for="city">Parola:</Label>
              <Col sm={7}>
                <Input style={{width: "110%"}} type={this.state.inputHideAndShoww ? "password" : "text"}
                       name="parola" value={this.state.selected.parola}
                       onChange={this.editHandleInputChange} {...this.editClassName("parola")} />
              </Col>
              <Col sm={1}>
                <button type="button"
                        className="close pull-right"
                        style={{outline: "none"}}
                        onClick={this.inputHideAndShoww}
                        title={this.state.inputHideAndShoww ? "Parolayı Göster" : "Parolayı Gizle"}>
                                    <span style={{fontSize: "66%", border: "none"}} aria-hidden="true"><i
                                      className="fa fa-eye"/></span>
                </button>
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={4} for="street">Şifre:</Label>
              <Col sm={7}>
                <Input style={{width: "110%"}} type={this.state.inputHideAndShow ? "password" : "text"}
                       name="sifre" value={this.state.selected.sifre}
                       onChange={this.editHandleInputChange} {...this.editClassName("sifre")} />
              </Col>
              <Col sm={1}>
                <button type="button"
                        className="close pull-right"
                        style={{outline: "none"}}
                        onClick={this.inputHideAndShow}
                        title={this.state.inputHideAndShow ? "Şifreyi Göster" : "Şİfreyi Gizle"}>
                                    <span style={{fontSize: "66%", border: "none"}} className="fa fa-eye"
                                          aria-hidden="true"/>
                </button>
              </Col>
            </FormGroup>
          </Form>
        </div>}
      </ModalBody>
      <ModalFooter>
        <Button title="İptal" style={{paddingLeft: '16px', width: '100px'}} color="secondary"
                onClick={() => this.setState({displayCreateUpdateDialog: false})}>
          İptal
        </Button>
        <Button title="Kaydet" style={{paddingLeft: '16px', width: '100px'}} color="primary"
                disabled={this.state.saveDisabledBtn}
                onClick={this.saveClicked}>
          Kaydet
        </Button>
      </ModalFooter>
      {this.state.createUpdateLoading && <ReactLoadingTop style={{width: "90px", height: "90px"}}/>}
    </Modal>
  }
}

export default KepAccountPage;