import {validateField} from "../../../../common/validation";


export function validDepartment(data, isCodeMust) {
    let error = {};
    let errorMessage="";
    error.name = validateField(undefined, "1,150", data.name);
    if(!error.name.valid){
        errorMessage = errorMessage + " <strong>Departman Adı " + error.name.message + "</strong><br/>";
    }
    error.typeId = validateField(undefined, "1,150", data.typeId);
    if(!error.typeId.valid){
        errorMessage = errorMessage + " <strong>Departman Tür " + error.typeId.message + "</strong><br/>";
    }
    error.parentId = validateField("number", "1,11", data.parentId);
    if(!error.parentId.valid){
        errorMessage = errorMessage + " <strong>Bağlı Olduğu Departman " + error.parentId.message + "</strong><br/>";
    }
    if (isCodeMust) {
      error.integrationCode = validateField(undefined, "1,150", data.integrationCode);
      if(!error.integrationCode.valid){
        errorMessage = errorMessage + " <strong>Departman Kodu " + error.integrationCode.message + "</strong><br/>";
      }
    }

    let valid = isValidObject(error);
    return {error: error, valid: valid,errorMessage:errorMessage};
}

