export function validFlowTemplate(flowTemplate) {
    let error = {};
    let errorMessage="";
    error.documentType = validateField("documentType", "1,100", flowTemplate.documentType);
    if(!error.documentType.valid){
        errorMessage = `${errorMessage} <strong>Belge Türü ${error.documentType.message}</strong><br/>`;
    }
    let valid = isValidObject(error);
    return {error: error, valid: valid,errorMessage:errorMessage};
}

export function validKepAccount(data) {
	let error = {};
	let errorMessage="";
    error.account = validateField("email", "1,100", data.account);
    if(!error.account.valid){
        errorMessage = errorMessage + " <strong>Kep Adresi " + error.account.message + "</strong><br/>";
    }
    error.tckn = validateField("number", "11,11", data.tckn);
    if(!error.tckn.valid){
        errorMessage = errorMessage + " <strong>TC Kimlik No " + error.tckn.message + "</strong><br/>";
    }
    error.parola = validateField(undefined, "1,20", data.parola);
    if(!error.parola.valid){
        errorMessage = errorMessage + " <strong>Parola " + error.parola.message + "</strong><br/>";
    }
    error.sifre = validateField(undefined, "1,20", data.sifre);
    if(!error.sifre.valid){
        errorMessage = errorMessage + " <strong>Şifre " + error.sifre.message + "</strong><br/>";
    }

    let valid = isValidObject(error);
	return {error: error, valid: valid,errorMessage:errorMessage};
}

export function isValidObject(error) {
	let valid = true;
	for (let key in error) {
		let tError = error[key];
		if (!tError.valid) {
			valid = false;
			break;
		}
	}
	return valid;
}


